 text-align:right;
 vertical-align:top;
 background-color:var(--global-divider-color);
 color:var(--global-divider-color)
}
.tg-times-start{
 background-color:#fff7d7;
 color:var(--global-divider-color);
 padding:1px 0 0 0;
 text-align:right;
 vertical-align:top;
 padding-right:2px
}
.tg-times-end{
 color:var(--global-divider-color);
 padding:1px 0 0 0;
 text-align:right;
 vertical-align:top;
 padding-right:2px;
 background-color:#d9f2db
}
.tg-time-pri{
 border-bottom:1px solid var(--global-divider-color);
 padding-right:2px
}
.tg-time-sec{
 padding-right:2px
}
.tg-time-pri-last,.tg-time-sec-last,.tg-times-start-last,.tg-times-end-last{
 border-bottom:none
}
.tg-time-sec,.tg-time-start,.tg-time-end{
 border-bottom:1px solid var(--global-divider-color)
}
.tg-timesnotlast{
 border-right:1px solid var(--global-divider-color)
}
.tg-col{
 border-left:1px solid var(--global-divider-color);
 vertical-align:top
}
.tg-col-today{
 border-left:1px solid #fad163;
 vertical-align:top
}
.tg-col-overlaywrapper{
 position:relative;
 height:0
}
.tg-gutter{
 position:relative;
 height:0;
 margin-right:10px
}
.tg-col-eventwrapper{
 position:relative;
 cursor:default
}
.tg-today{
 background-color:#fff7d7
}
.bg-exists .tg-today{
 background-color:var(--global-divider-color);
 opacity:.5;
 filter:alpha(opacity=50)
}
.tg-spanningwrapper{
 position:relative;
 margin:1px 0 0 1px
}
.tg-chipspanningwrapper{
 line-height:0;
 margin:0 10px 0 1px;
 z-index:1
}
.tg-hourmarkers{
 position:absolute;
 width:100%
}
.tg-markercell{
 height:42px
}
.tg-markercell60{
 height:60px
}
.tg-dualmarker,.tg-dualmarker60{
 border-bottom:1px dotted var(--global-divider-color);
 border-top:1px solid var(--global-divider-color);
 font-size:1px
}
.tg-dualmarker{
 height:20px;
 margin-bottom:20px
}
.tg-dualmarker60{
 height:29px;
 margin-bottom:29px
}
.tg-nowmarker{
 position:absolute;
 left:0;
 width:100%;
 top:0;
 height:0;
 border-top:2px solid var(--global-theme-color);
 overflow:hidden
}
.tg-nowptr{
 position:absolute;
 background-position:-60px -68px;
 width:5px;
 height:9px;
 background-image:url(https://calendar.google.com/googlecalendar/images/combined_v22.png)
}
.rtl .tg-nowptr{
 background-position:-67px -68px
}
.wk-tzlabel{
 color:var(--global-divider-color);
 vertical-align:bottom;
 text-align:center;
 overflow:hidden;
 padding-bottom:2px
}
.wk-moreevents{
 padding:10px
}
.wk-moreevents .st-c{
 padding:1px 2px 0 2px;
 border:none
}
.wk-sideevents{
 width:20%;
 font-family:Verdana,sans-serif;
 vertical-align:top;
 padding-top:1px
}
.wk-more-prealign{
 text-align:right
}
.wk-disclose-pos{
 position:absolute;
 top:0;
 left:0
}
.wk-disclose{
 position:absolute;
 top:2em;
 right:2px
}
.wk-zip{
 cursor:pointer;
 width:12px;
 height:12px;
 background-image:url(https://calendar.google.com/googlecalendar/images/combined_v22.png);
 background-position:-216px 0
}
.goog-zippy-collapsed .wk-zip{
 background-position:-216px -12px
}
.wk-promo{
 overflow:hidden;
 width:100%;
 white-space:normal
}
.dp-popup{
 position:absolute;
 border:1px solid var(--global-text-color);
 background:#ccc;
 font-size:9pt;
 line-height:1em;
 width:170px;
 z-index:25000003
}
.dp-dayh{
 font-size:78%
}
.dp-monthtable{
 width:100%;
 padding:0;
 -webkit-user-select:none
}
.dp-cell{
 text-align:center;
 padding:3px 2px;
 font-family:Arial,sans-serif;
 font-size:12px;
 color:var(--global-text-color);
 font-weight:normal
}
.dp-heading{
 cursor:pointer;
 font-weight:bold;
 vertical-align:middle
}
.dp-cur{
 font-weight:bold;
 padding-bottom:4px;
 text-align:center;
 white-space:nowrap;
 font-size:100%;
 color:var(--global-theme-color)
}
.dp-prev,.dp-next{
 font-size:100%;
 padding-bottom:6px;
 cursor:pointer;
 color:var(--global-theme-color)
}
.dp-prev{
 text-align:right
}
.dp-next{
 text-align:left
}
.dp-sb-cur{
 color:var(--global-theme-color);
 cursor:pointer;
 font-size:13px;
 font-weight:bold;
 padding:7px 0;
 text-align:left;
 text-decoration:underline;
 -webkit-user-select:none
}
.dp-sb-nav{
 padding:7px 0;
 text-align:right;
 white-space:nowrap
}
.dp-sb-prev{
 background:url(/assets/img/combined_v22.png) -243px -35px no-repeat;
 cursor:pointer;
 height:12px;
 padding:0;
 width:15px
}
.dp-sb-next{
 background:url(/assets/img/combined_v22.png) -243px -35px no-repeat;
 cursor:pointer;
 height:12px;
 padding:0;
 width:15px;
 background-position:-258px -35px;
 margin-left:3px
}
.rtl .dp-sb-prev{
 background-position:-258px -35px;
 margin-left:3px
}
.rtl .dp-sb-next{
 background-position:-243px -35px
}
.dp-weekday{
 background:var(--global-bg-color)
}
.dp-weekday-selected{
 color:var(--global-text-color);
 background:var(--global-divider-color)
}
.dp-weekend,.dp-weekendh{
 background:var(--global-bg-color)
}
.dp-weekend-selected{
 color:var(--global-text-color);
 background:var(--global-divider-color)
}
.dp-with-events{
 font-weight:bold!important
}
.dp-offmonth{
 color:var(--global-divider-color)
}
.dp-offmonth-selected{
 color:var(--global-divider-color)
}
.dp-monthtablediv{
 border:1px solid var(--global-divider-color)
}
#nav .dp-monthtablediv{
 border:none
}
.dp-today,.dp-today-selected{
 padding:1px;
 color:var(--global-text-color);
 border:1px solid
}
.dp-today{
 background:var(--global-theme-color);
 border-color:var(--global-theme-color)
}
.dp-today-selected{
 background:var(--global-theme-color);
 border-color:var(--global-theme-color)
}
.dp-onhover{
 background-color:var(--global-divider-color);
 border-color:var(--global-divider-color);
 color:var(--global-text-color);
}
.dp-today.dp-onhover,.dp-today-selected.dp-onhover{
 border-color:var(--global-theme-color);
}
@import url("//fonts.googleapis.com/css?family=Product+Sans");
.logo-plus-button{
 height:18px;
 background-color:var(--global-bg-color);
 padding-right:2px
}
.logo-plus-button,.logo-plus-button>div{
 display:inline-block
}
.logo-plus-button-plus-icon{
 font-family:Arial;
 background-color:var(--global-theme-color);
 background-image:url(https://calendar.google.com/googlecalendar/images/logo-plus.png);
 background-position:center center;
 width:18px;
 height:18px
}
.logo-plus-button-lockup{
 padding-left:46px;
 background-image:url(https://www.gstatic.com/images/branding/googlelogo/1x/googlelogo_color_46x16dp.png);
 background-size:46px 16px;
 background-repeat:no-repeat;
 background-position-y:1px;
 position:relative;
 top:-4px;
 margin-left:2px;
 margin-right:1px
}
.logo-plus-button-lockup-text{
 font-family:"Product Sans",Arial,Helvetica,sans-serif;
 font-size:13px;
 opacity:.54
}
